.toasted-container .toasted.primary.success,
.toasted-container .toasted.bubble.success {
  background: #00b5ad;
}
.toasted-container .toasted.primary.error,
.toasted-container .toasted.bubble.error {
  background: #dd4b39;
}
.toasted-container .toasted.primary.info,
.toasted-container .toasted.bubble.info {
  background: #42A5F5;
}
.toasted-container .toasted.outline.success {
  color: #00b5ad;
  border-color: #00b5ad;
}
.toasted-container .toasted.outline.error {
  color: #dd4b39;
  border-color: #dd4b39;
}
.toasted-container .toasted.outline.info {
  color: #42A5F5;
  border-color: #42A5F5;
}
.toasted-container .toasted.primary {
  -webkit-font-smoothing: auto;
  border-radius: .4rem;
}
.toasted-container .toasted.bubble {
  -webkit-font-smoothing: auto;
}
.toasted-container .toasted.outline {
  font-weight: normal;
}
:root {
  font-size: 14px;
}
*,
:after,
:before {
  box-sizing: border-box;
}
body {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  color: #444;
  -webkit-font-smoothing: antialiased;
  margin: 0;
  padding: 0;
  background-image: url('~assets/img/login/bg.png');
  background-size: cover;
}
#app {
  min-height: calc(100vh - 90px);
  padding-top: 5px;
  padding-bottom: 5px;
}
.footer p {
  margin: 0;
  padding: 12px 0;
  text-align: center;
  font-size: 12px;
  color: #298ee3;
  line-height: 1.5;
}
.ui.tiny.modal > .header {
  font-size: 16px;
  padding-top: 14px;
  padding-bottom: 14px;
}
.plain-prompt {
  min-height: calc(100vh - 90px);
  text-align: center;
  padding-top: 4em;
}
.plain-prompt a {
  color: #298ee3;
}
.ui.tiny.modal p {
  margin: 10px 0;
}



